@import url(https://fonts.googleapis.com/css?family=Roboto:300,400,500,700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Bowlby+One+SC&display=swap);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* ---------- FONTS ---------- */
/* Roboto */
/* Capitalized and Bold Font -  Bowlby One SC */
/* Material-UI Font Icons */
/* Night at the Opera Theatre font */
@font-face {
  font-family: 'NightAtTheOpera';
  src: local('NightAtTheOpera'), url(/shoppies-movie-nominations/static/media/NightAtTheOperaNF.c32b37e1.ttf) format('truetype');
  font-weight: normal;
  font-style: normal;
}
/* Broadyway Font */
@font-face {
  font-family: 'Broady3DFilled';
  src: local('Broady3DFilled'), url(/shoppies-movie-nominations/static/media/Broadway3DFilled.294eb7d3.ttf) format('truetype');
}
@font-face {
  font-family: 'Broadway3D';
  src: local('Broadway3D'), url(/shoppies-movie-nominations/static/media/Broadway3D.9c794e6f.ttf) format('truetype');
}
@font-face {
  font-family: 'Broadway3DItalic';
  src: local('Broadway3DItalic'), url(/shoppies-movie-nominations/static/media/Broadway3D-Italic.92f31b1a.ttf) format('truetype');
}
/* ---------- GENERAL APP STYLING ---------- */
html, div#root {
  height: 100%;
}

body {
  margin: 0;
  color: #eeeeee;
  font-family: "Roboto", sans-serif;
  background-color: #16161B;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.App {
  text-align: center;
  height: 100%;
}

/* ---------- HEADER ---------- */
/* Center header vertically and horizontally */
.App-header {
  background-color: #040404;
  min-height: 12vh;
  display: flex;
  padding: 3% 0 2% 0;
  flex-direction: column  ;
  align-items: center;
  justify-content: center;
  color: white;
}

div#brand{
  background-color: #040404;
  font-family: 'Broadway3D', sans-serif;
  display: flex;
  /* padding-top: 0.1em; */
  flex-direction: row;
  align-items: center;
  justify-content: center;
  font-size: 14vw;
  color: white;
  width: 100%;
}

img#logo-img{
  margin: 0 2% 2% 0;
  height: 10vw;
  width: auto;
}

div#contact {
  font-family: 'Roboto', sans-serif;
  font-size: 0.9em;
}

i.italics {
  padding-right:0.5em;
}

/* ---------- Main Container ---------- */
#main-container {
  padding-top: 2em;
  padding-bottom: 5em;
}
/* ---------- SEARCH BAR ---------- */
#movie-search-bar {
  width: 80%;
  font-size: 1.3em;
  font-weight: 500;
  padding: 2%;
  border: block;
  border-radius: 300px;
  margin: 2% 0%;
}

/* #movie-search-bar input:focus{
  outline: none;
  border: 1px solid #64943E;
} */

/* ---------- NOMINATION DRAWER TITLE ----------*/
h2#nomination-list{
  font-family: 'Broadway3D', sans-serif;
}

/* ----------- MOVIE CARDS ------------*/
img.movie-thumbnail {
  height: auto;
  width: 150px;
  border-radius: 5px;
}

/* ---------- NOMINATE BUTTONS ------------*/
button.nominate-btn, button.remove-nominee-btn {
  margin:0 1em 0.2em 1em;
  border-radius: 30px;
}

/* ---------- NOMINATION DRAWER ------------*/
div#nomination-drawer {
  background-color: #383845;
}
